	module.exports = {"data":{"markdownRemark":{"html":"<p>TVはゼロで、「Ten Days of Confusion」と呼ばれるフランス映画を展示します。私は退屈な気分になり始めました。それはフランスの映画のためでした。後で私はそれが良い映画であることを知った。フランス語では、L'APPETITVIENTENMANGEANGです。精神病を患っていて、教師を家に持ち帰っている青年です。家族は豊かで、父親は養子父、妻は若いです。彼は妻と不倫関係を持ち、父親は実際にそれを知っていた。彼はそれらを拷問するために様々な方法を用いた。</p>\n<p>最後に、妻が殺され、学生は自殺した。外観は生徒が妻を殺したことです。分析の後、教師はおそらくフロイトの分析であり、実際の殺人犯は養子父親であることが分かった。養子の父は神の役割を果たすためです。簡単なストーリー表示ではなく、文化的な「深み」があります。</p>\n<p>私は武漢で発見された \"Shicheng Tongue War\"の海賊版を見ました。 「現代西洋政治思想」の過程で、「ベルの技術者政治思想」と「トフラーの未来的政治思想」を追加する。両国の政治思想は、産業革命後の社会の発展の方向性を表し、工業化に起因する社会的、文化的、心理的な葛藤をより詳細に分析し、近代化している諸国に影響を及ぼす。ベルは資本主義社会をより批判している。トフラーは現在の資本主義体系は将来の発展には適していないとも述べているが、批判ではなく歴史的な超越である。</p>\n<p>「西洋政治の古典の選択」では、ハバーマスの「コミュニケーションと社会進歩」、ヴェーバーの「プロテスタント倫理と資本主義」、ダールの「多様化した民主主義のジレンマ」、ノジックの「なし政府、州、ユートピア。これらの書籍は、元の本の一部として更新されます。問題は、本の中国語版でのみ、本を見つけることはしばしば容易ではないということです。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0038.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0038.md","frontmatter":{"title":"2月21日 月曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0038.md absPath of file >>> MarkdownRemark"}}

